import React from 'react';
import { Link } from 'react-router-dom';
import { Api } from '../../../utils/api';
import Item from '../../Items/Item';

class Ni単o extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            items: []
        };

        this.ni単osRef = React.createRef();
    }

    componentDidMount() {
        Api.getKids().then(kidItems => {
            this.setState({ items: kidItems });
        })
    }

    render() {
        return (
            <div id='ni単os'>
                <h3 className="tipo">Ni単os</h3>
                <div className="item-list-catalogo">
                    {this.state.items.map(kidItem => {
                        return (
                            <Link to={`/catalogo/${kidItem.sku}`}>
                                <Item key={kidItem.id}
                                    sku={kidItem.sku}
                                    nombre={kidItem.nombre}
                                    precioActual={kidItem.precio_actual}
                                    precioAnterior={kidItem.precio_anterior}
                                    img={kidItem.img} />
                            </Link>
                        )
                    })}
                </div>
            </div>
        );
    }
}

export default Ni単o;